.drawer {
  display: block;
  position: absolute;
  z-index: 20;
  transition: all 0.3s cubic-bezier(0.1, 0.7, 0.1, 1); }
  .drawer--mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 0;
    opacity: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.2); }
  .drawer--is-open {
    z-index: 100;
    opacity: 1;
    visibility: visible; }
  .drawer__content {
    padding: 15px 20px;
    width: 100%;
    height: 50%;
    display: block;
    overflow: hidden;
    position: absolute;
    bottom: 0;
    background-color: #fff;
    z-index: 50; }
    .drawer__content--grid .grid__item {
      padding-left: 24px;
      padding-right: 24px; }
      .drawer__content--grid .grid__item:hover {
        background: rgba(0, 0, 0, 0.2); }
    .drawer__content--grid a {
      text-align: center;
      padding: 5px 12px;
      text-decoration: none;
      display: block; }
      .drawer__content--grid a i {
        height: 48px;
        width: 48px;
        font-size: 48px;
        display: block;
        margin-bottom: 6px; }
  .drawer--fixed {
    position: fixed; }
  .drawer--dark {
    background-color: rgba(0, 0, 0, 0.3);
    color: #fff; }
    .drawer--dark drawer__button {
      color: #fff; }
  .drawer--left {
    left: 0;
    top: 0;
    bottom: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    width: 25%; }
    .drawer--left.is-open {
      -webkit-transform: translateX(0);
      transform: translateX(0); }
  .drawer--right {
    top: 0;
    bottom: 0;
    right: 0;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    width: 25%; }
    .drawer--right.is-open {
      -webkit-transform: translateX(0);
      transform: translateX(0); }
  .drawer--top {
    top: 0;
    right: 0;
    left: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    width: 100%; }
    .drawer--top.is-open {
      -webkit-transform: translateY(0);
      transform: translateY(0); }
  .drawer--bottom {
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    -webkit-transform: translateY(100%);
    transform: translateY(100%); }
    .drawer--bottom.is-open {
      -webkit-transform: translateY(0);
      transform: translateY(0); }
